"use client";

export class FrameInterpolator {
  private canvas: HTMLCanvasElement;
  private ctx: CanvasRenderingContext2D;

  constructor() {
    this.canvas = document.createElement('canvas');
    this.ctx = this.canvas.getContext('2d')!;
  }

  // Generate intermediate frames between two images
  async generateIntermediateFrames(
    image1Url: string, 
    image2Url: string, 
    frameCount: number = 5
  ): Promise<string[]> {
    const img1 = await this.loadImage(image1Url);
    const img2 = await this.loadImage(image2Url);
    
    // Set canvas size to match images
    this.canvas.width = Math.max(img1.width, img2.width);
    this.canvas.height = Math.max(img1.height, img2.height);
    
    const frames: string[] = [];
    
    // Generate intermediate frames using alpha blending
    for (let i = 0; i <= frameCount; i++) {
      const alpha = i / frameCount;
      const frame = await this.blendImages(img1, img2, alpha);
      frames.push(frame);
    }
    
    return frames;
  }

  // Create smooth 360Â° rotation from multiple images
  async create360Rotation(imageUrls: string[], totalFrames: number = 60): Promise<string[]> {
    if (imageUrls.length < 2) {
      throw new Error('At least 2 images required for rotation');
    }

    const images = await Promise.all(imageUrls.map(url => this.loadImage(url)));
    const frames: string[] = [];
    
    // Calculate frames per segment
    const framesPerSegment = Math.floor(totalFrames / imageUrls.length);
    
    for (let i = 0; i < imageUrls.length; i++) {
      const currentImage = images[i];
      const nextImage = images[(i + 1) % images.length];
      
      // Generate intermediate frames between current and next image
      const segmentFrames = await this.generateIntermediateFrames(
        imageUrls[i],
        imageUrls[(i + 1) % imageUrls.length],
        framesPerSegment
      );
      
      frames.push(...segmentFrames);
    }
    
    // Ensure we have exactly the requested number of frames
    while (frames.length < totalFrames) {
      frames.push(frames[frames.length - 1]);
    }
    
    return frames.slice(0, totalFrames);
  }

  // Advanced frame interpolation with rotation effect
  async createRotationFrames(
    imageUrls: string[], 
    totalFrames: number = 60,
    rotationStyle: 'smooth' | 'discrete' | '3d' = 'smooth'
  ): Promise<string[]> {
    const images = await Promise.all(imageUrls.map(url => this.loadImage(url)));
    const frames: string[] = [];
    
    // Set canvas size
    const maxWidth = Math.max(...images.map(img => img.width));
    const maxHeight = Math.max(...images.map(img => img.height));
    this.canvas.width = maxWidth;
    this.canvas.height = maxHeight;
    
    for (let frameIndex = 0; frameIndex < totalFrames; frameIndex++) {
      const rotationProgress = frameIndex / totalFrames;
      const frame = await this.createRotationFrame(images, rotationProgress, rotationStyle);
      frames.push(frame);
    }
    
    return frames;
  }

  private async createRotationFrame(
    images: HTMLImageElement[], 
    progress: number, 
    style: 'smooth' | 'discrete'
  ): Promise<string> {
    this.ctx.clearRect(0, 0, this.canvas.width, this.canvas.height);
    
    if (style === 'discrete') {
      // Show discrete images at specific angles
      const imageIndex = Math.floor(progress * images.length) % images.length;
      const img = images[imageIndex];
      
      // Center the image
      const x = (this.canvas.width - img.width) / 2;
      const y = (this.canvas.height - img.height) / 2;
      
      this.ctx.drawImage(img, x, y);
    } else if (style === '3d') {
      // 3D rotation effect
      const imageIndex = Math.floor(progress * images.length) % images.length;
      const img = images[imageIndex];
      
      this.ctx.save();
      this.ctx.translate(this.canvas.width / 2, this.canvas.height / 2);
      
      // Apply 3D Y-axis rotation
      const angle = progress * Math.PI * 2;
      const scaleX = Math.abs(Math.cos(angle));
      this.ctx.scale(scaleX, 1);
      
      const x = -img.width / 2;
      const y = -img.height / 2;
      this.ctx.drawImage(img, x, y);
      this.ctx.restore();
    } else {


      // Smooth blending between images
      const scaledProgress = progress * images.length;
      const currentIndex = Math.floor(scaledProgress) % images.length;
      const nextIndex = (currentIndex + 1) % images.length;
      const blendFactor = scaledProgress - Math.floor(scaledProgress);
      
      const currentImg = images[currentIndex];
      const nextImg = images[nextIndex];
      
      // Draw current image
      const x1 = (this.canvas.width - currentImg.width) / 2;
      const y1 = (this.canvas.height - currentImg.height) / 2;
      this.ctx.globalAlpha = 1 - blendFactor;
      this.ctx.drawImage(currentImg, x1, y1);
      
      // Blend with next image
      const x2 = (this.canvas.width - nextImg.width) / 2;
      const y2 = (this.canvas.height - nextImg.height) / 2;
      this.ctx.globalAlpha = blendFactor;
      this.ctx.drawImage(nextImg, x2, y2);
      
      this.ctx.globalAlpha = 1; // Reset alpha
    }
    
    return new Promise((resolve) => {
      this.canvas.toBlob((blob) => {
        if (blob) {
          resolve(URL.createObjectURL(blob));
        }
      }, 'image/png');
    });
  }

  private async blendImages(
    img1: HTMLImageElement, 
    img2: HTMLImageElement, 
    alpha: number
  ): Promise<string> {
    this.ctx.clearRect(0, 0, this.canvas.width, this.canvas.height);
    
    // Draw first image
    const x1 = (this.canvas.width - img1.width) / 2;
    const y1 = (this.canvas.height - img1.height) / 2;
    this.ctx.globalAlpha = 1 - alpha;
    this.ctx.drawImage(img1, x1, y1);
    
    // Blend second image
    const x2 = (this.canvas.width - img2.width) / 2;
    const y2 = (this.canvas.height - img2.height) / 2;
    this.ctx.globalAlpha = alpha;
    this.ctx.drawImage(img2, x2, y2);
    
    this.ctx.globalAlpha = 1; // Reset alpha
    
    return new Promise((resolve) => {
      this.canvas.toBlob((blob) => {
        if (blob) {
          resolve(URL.createObjectURL(blob));
        }
      }, 'image/png');
    });
  }

  private loadImage(url: string): Promise<HTMLImageElement> {
    return new Promise((resolve, reject) => {
      const img = new Image();
      img.crossOrigin = 'anonymous';
      img.onload = () => resolve(img);
      img.onerror = () => reject(new Error(`Failed to load image: ${url}`));
      img.src = url;
    });
  }

  // Create frames with 3D rotation effect
  async create3DRotationFrames(
    imageUrls: string[], 
    totalFrames: number = 60,
    rotationAxis: 'y' | 'x' | 'z' = 'y'
  ): Promise<string[]> {
    const images = await Promise.all(imageUrls.map(url => this.loadImage(url)));
    const frames: string[] = [];
    
    const maxWidth = Math.max(...images.map(img => img.width));
    const maxHeight = Math.max(...images.map(img => img.height));
    this.canvas.width = maxWidth;
    this.canvas.height = maxHeight;
    
    for (let frameIndex = 0; frameIndex < totalFrames; frameIndex++) {
      const angle = (frameIndex / totalFrames) * 2 * Math.PI;
      const frame = await this.create3DFrame(images, angle, rotationAxis);
      frames.push(frame);
    }
    
    return frames;
  }

  private async create3DFrame(
    images: HTMLImageElement[], 
    angle: number, 
    axis: 'y' | 'x' | 'z'
  ): Promise<string> {
    this.ctx.clearRect(0, 0, this.canvas.width, this.canvas.height);
    
    // Calculate which image to show based on angle
    const normalizedAngle = (angle % (2 * Math.PI)) / (2 * Math.PI);
    const imageIndex = Math.floor(normalizedAngle * images.length);
    const img = images[imageIndex];
    
    // Apply 3D transformation
    this.ctx.save();
    this.ctx.translate(this.canvas.width / 2, this.canvas.height / 2);
    
    if (axis === 'y') {
      // Y-axis rotation (most common for product rotation)
      const scaleX = Math.cos(angle);
      this.ctx.scale(Math.abs(scaleX), 1);
    } else if (axis === 'x') {
      // X-axis rotation
      const scaleY = Math.cos(angle);
      this.ctx.scale(1, Math.abs(scaleY));
    }
    
    this.ctx.drawImage(img, -img.width / 2, -img.height / 2);
    this.ctx.restore();
    
    return new Promise((resolve) => {
      this.canvas.toBlob((blob) => {
        if (blob) {
          resolve(URL.createObjectURL(blob));
        }
      }, 'image/png');
    });
  }
}
