"use client";

import { useState } from "react";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Label } from "@/components/ui/label";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Switch } from "@/components/ui/switch";
import { Slider } from "@/components/ui/slider";
import { Play, Download, Loader2, Wand2 } from "lucide-react";
import { WorkingVideoGenerator } from "@/lib/working-video-generator";

interface WorkingVideoGeneratorProps {
  images: File[];
  onVideoGenerated: (videoUrl: string) => void;
}

export function WorkingVideoGeneratorComponent({ images, onVideoGenerated }: WorkingVideoGeneratorProps) {
  const [isGenerating, setIsGenerating] = useState(false);
  const [progress, setProgress] = useState(0);
  const [status, setStatus] = useState("");
  const [error, setError] = useState<string | null>(null);
  
  const [config, setConfig] = useState({
    removeBackground: true,
    fps: 30,
    duration: 3,
    quality: '1080p' as '720p' | '1080p' | '4k',
    format: 'mp4' as 'mp4' | 'webm' | 'gif',
    rotationStyle: 'smooth' as 'smooth' | 'discrete' | '3d'
  });

  const generateVideo = async () => {
    if (images.length < 2) {
      setError("Please upload at least 2 images");
      return;
    }

    setIsGenerating(true);
    setProgress(0);
    setStatus("Starting video generation...");
    setError(null);

    try {
      const generator = new WorkingVideoGenerator();
      
      const videoUrl = await generator.generateRotationVideo(
        images,
        config,
        (progress, status) => {
          setProgress(progress);
          setStatus(status);
        }
      );

      onVideoGenerated(videoUrl);
      setStatus("Video generated successfully!");
      
    } catch (error) {
      console.error("Video generation failed:", error);
      setError(error instanceof Error ? error.message : "Video generation failed");
    } finally {
      setIsGenerating(false);
    }
  };

  return (
    <Card className="bg-gray-900 border-gray-800">
      <CardHeader>
        <CardTitle className="text-white flex items-center gap-2">
          <Wand2 className="h-5 w-5" />
          Working Video Generator
        </CardTitle>
        <CardDescription className="text-gray-400">
          Generate real 360Â° rotation videos with background removal and frame interpolation
        </CardDescription>
      </CardHeader>
      <CardContent className="space-y-6">
        {/* Background Removal */}
        <div className="flex items-center justify-between">
          <Label className="text-white">Remove Background</Label>
          <Switch
            checked={config.removeBackground}
            onCheckedChange={(checked) => setConfig({ ...config, removeBackground: checked })}
          />
        </div>

        {/* Rotation Style */}
        <div>
          <Label className="text-white mb-2 block">Rotation Style</Label>
          <Select
            value={config.rotationStyle}
            onValueChange={(value: 'smooth' | 'discrete' | '3d') => 
              setConfig({ ...config, rotationStyle: value })
            }
          >
            <SelectTrigger className="bg-gray-800 border-gray-700 text-white">
              <SelectValue />
            </SelectTrigger>
            <SelectContent className="bg-gray-800 border-gray-700">
              <SelectItem value="smooth">Smooth Blending</SelectItem>
              <SelectItem value="discrete">Discrete Frames</SelectItem>
              <SelectItem value="3d">3D Rotation Effect</SelectItem>
            </SelectContent>
          </Select>
        </div>

        {/* Duration */}
        <div>
          <Label className="text-white mb-2 block">Duration: {config.duration} seconds</Label>
          <Slider
            value={[config.duration]}
            onValueChange={([value]) => setConfig({ ...config, duration: value })}
            min={2}
            max={10}
            step={1}
            className="w-full"
          />
        </div>

        {/* FPS */}
        <div>
          <Label className="text-white mb-2 block">Frame Rate</Label>
          <Select
            value={config.fps.toString()}
            onValueChange={(value) => setConfig({ ...config, fps: parseInt(value) })}
          >
            <SelectTrigger className="bg-gray-800 border-gray-700 text-white">
              <SelectValue />
            </SelectTrigger>
            <SelectContent className="bg-gray-800 border-gray-700">
              <SelectItem value="24">24 FPS</SelectItem>
              <SelectItem value="30">30 FPS</SelectItem>
              <SelectItem value="60">60 FPS</SelectItem>
            </SelectContent>
          </Select>
        </div>

        {/* Quality */}
        <div>
          <Label className="text-white mb-2 block">Video Quality</Label>
          <Select
            value={config.quality}
            onValueChange={(value: '720p' | '1080p' | '4k') => 
              setConfig({ ...config, quality: value })
            }
          >
            <SelectTrigger className="bg-gray-800 border-gray-700 text-white">
              <SelectValue />
            </SelectTrigger>
            <SelectContent className="bg-gray-800 border-gray-700">
              <SelectItem value="720p">720p (HD)</SelectItem>
              <SelectItem value="1080p">1080p (Full HD)</SelectItem>
              <SelectItem value="4k">4K (Ultra HD)</SelectItem>
            </SelectContent>
          </Select>
        </div>

        {/* Format */}
        <div>
          <Label className="text-white mb-2 block">Output Format</Label>
          <Select
            value={config.format}
            onValueChange={(value: 'mp4' | 'webm' | 'gif') => 
              setConfig({ ...config, format: value })
            }
          >
            <SelectTrigger className="bg-gray-800 border-gray-700 text-white">
              <SelectValue />
            </SelectTrigger>
            <SelectContent className="bg-gray-800 border-gray-700">
              <SelectItem value="mp4">MP4</SelectItem>
              <SelectItem value="webm">WebM</SelectItem>
              <SelectItem value="gif">GIF</SelectItem>
            </SelectContent>
          </Select>
        </div>

        {/* Progress */}
        {isGenerating && (
          <div className="space-y-2">
            <div className="flex justify-between text-sm">
              <span className="text-white">{status}</span>
              <span className="text-gray-400">{Math.round(progress)}%</span>
            </div>
            <div className="w-full bg-gray-700 rounded-full h-2">
              <div 
                className="bg-blue-600 h-2 rounded-full transition-all duration-300"
                style={{ width: `${progress}%` }}
              />
            </div>
          </div>
        )}

        {/* Error Message */}
        {error && (
          <div className="text-red-400 text-sm bg-red-900/20 p-3 rounded">
            {error}
          </div>
        )}

        {/* Generate Button */}
        <Button
          onClick={generateVideo}
          disabled={isGenerating || images.length < 2}
          className="w-full"
          size="lg"
        >
          {isGenerating ? (
            <>
              <Loader2 className="mr-2 h-4 w-4 animate-spin" />
              Generating Video...
            </>
          ) : (
            <>
              <Play className="mr-2 h-4 w-4" />
              Generate 360Â° Video
            </>
          )}
        </Button>

        {/* Info */}
        <div className="text-xs text-gray-500 space-y-1">
          <p>â¢ Automatically removes backgrounds from uploaded images</p>
          <p>â¢ Creates smooth transitions between frames</p>
          <p>â¢ Generates professional 360Â° rotation videos</p>
          <p>â¢ Supports multiple output formats and qualities</p>
        </div>
      </CardContent>
    </Card>
  );
}
