"use client";

import { BackgroundRemover } from './background-remover';
import { FrameInterpolator } from './frame-interpolator';

export class WorkingVideoGenerator {
  private backgroundRemover: BackgroundRemover;
  private frameInterpolator: FrameInterpolator;
  private canvas: HTMLCanvasElement;
  private ctx: CanvasRenderingContext2D;

  constructor(apiKey?: string) {
    this.backgroundRemover = new BackgroundRemover(apiKey);
    this.frameInterpolator = new FrameInterpolator();
    this.canvas = document.createElement('canvas');
    this.ctx = this.canvas.getContext('2d')!;
  }

  async generateRotationVideo(
    imageFiles: File[],
    options: {
      removeBackground: boolean;
      fps: number;
      duration: number;
      quality: '720p' | '1080p' | '4k';
      format: 'mp4' | 'webm' | 'gif';
      rotationStyle: 'smooth' | 'discrete' | '3d';
    },
    onProgress?: (progress: number, status: string) => void
  ): Promise<string> {
    
    if (imageFiles.length < 2) {
      throw new Error('At least 2 images required');
    }

    const totalSteps = imageFiles.length + 3; // Process images + generate frames + create video
    let currentStep = 0;

    try {
      // Step 1: Process uploaded images
      onProgress?.(0, 'Processing uploaded images...');
      const processedImageUrls: string[] = [];

      for (const file of imageFiles) {
        const imageUrl = URL.createObjectURL(file);
        
        if (options.removeBackground) {
          onProgress?.(
            (currentStep / totalSteps) * 100, 
            `Removing background from image ${currentStep + 1}...`
          );
          
          try {
            // Try API first, fallback to canvas method
            const processedUrl = await this.backgroundRemover.removeBackgroundCanvas(imageUrl);
            processedImageUrls.push(processedUrl);
          } catch (error) {
            console.warn('Background removal failed, using original image:', error);
            processedImageUrls.push(imageUrl);
          }
        } else {
          processedImageUrls.push(imageUrl);
        }
        
        currentStep++;
      }

      // Step 2: Generate rotation frames
      onProgress?.(
        (currentStep / totalSteps) * 100, 
        'Generating rotation frames...'
      );

      const totalFrames = options.fps * options.duration;
      let frames: string[] = [];

      switch (options.rotationStyle) {
        case 'smooth':
          frames = await this.frameInterpolator.createRotationFrames(
            processedImageUrls, 
            totalFrames, 
            'smooth'
          );
          break;
        case 'discrete':
          frames = await this.frameInterpolator.createRotationFrames(
            processedImageUrls, 
            totalFrames, 
            'discrete'
          );
          break;
        case '3d':
          frames = await this.frameInterpolator.create3DRotationFrames(
            processedImageUrls, 
            totalFrames, 
            'y'
          );
          break;
      }

      currentStep++;

      // Step 3: Set up canvas for video recording
      onProgress?.(
        (currentStep / totalSteps) * 100, 
        'Setting up video recording...'
      );

      const qualityMap = {
        '720p': { width: 1280, height: 720 },
        '1080p': { width: 1920, height: 1080 },
        '4k': { width: 3840, height: 2160 }
      };

      const { width, height } = qualityMap[options.quality];
      this.canvas.width = width;
      this.canvas.height = height;

      currentStep++;

      // Step 4: Create video
      onProgress?.(
        (currentStep / totalSteps) * 100, 
        'Creating video...'
      );

      if (options.format === 'gif') {
        return await this.createGIF(frames, options.fps, onProgress);
      } else {
        return await this.createVideo(frames, options.fps, options.format, onProgress);
      }

    } catch (error) {
      console.error('Video generation failed:', error);
      throw new Error(`Video generation failed: ${error instanceof Error ? error.message : 'Unknown error'}`);
    }
  }

  private async createVideo(
    frames: string[], 
    fps: number, 
    format: 'mp4' | 'webm',
    onProgress?: (progress: number, status: string) => void
  ): Promise<string> {
    
    // Check MediaRecorder support
    if (!window.MediaRecorder) {
      throw new Error('MediaRecorder not supported in this browser');
    }

    // Determine MIME type
    const mimeTypes = [
      `video/${format};codecs=vp9`,
      `video/${format};codecs=vp8`,
      `video/${format}`,
      'video/webm',
      'video/mp4'
    ];

    let selectedMimeType = '';
    for (const mimeType of mimeTypes) {
      if (MediaRecorder.isTypeSupported(mimeType)) {
        selectedMimeType = mimeType;
        break;
      }
    }

    if (!selectedMimeType) {
      throw new Error('No supported video format found');
    }

    // Set up MediaRecorder
    const stream = this.canvas.captureStream(fps);
    const mediaRecorder = new MediaRecorder(stream, {
      mimeType: selectedMimeType,
      videoBitsPerSecond: 5000000 // 5 Mbps for high quality
    });

    const chunks: Blob[] = [];
    mediaRecorder.ondataavailable = (event) => {
      if (event.data.size > 0) {
        chunks.push(event.data);
      }
    };

    return new Promise((resolve, reject) => {
      mediaRecorder.onstop = () => {
        const blob = new Blob(chunks, { type: selectedMimeType });
        const url = URL.createObjectURL(blob);
        resolve(url);
      };

      mediaRecorder.onerror = (error) => {
        reject(new Error(`MediaRecorder error: ${error}`));
      };

      mediaRecorder.start();

      let frameIndex = 0;
      const renderFrame = async () => {
        if (frameIndex >= frames.length) {
          mediaRecorder.stop();
          return;
        }

        // Load and draw frame
        const img = new Image();
        img.onload = () => {
          this.ctx.clearRect(0, 0, this.canvas.width, this.canvas.height);
          
          // Center the image
          const x = (this.canvas.width - img.width) / 2;
          const y = (this.canvas.height - img.height) / 2;
          this.ctx.drawImage(img, x, y);
          
          onProgress?.(
            (frameIndex / frames.length) * 100,
            `Rendering frame ${frameIndex + 1}/${frames.length}...`
          );

          frameIndex++;
          setTimeout(renderFrame, 1000 / fps);
        };

        img.onerror = () => {
          reject(new Error(`Failed to load frame ${frameIndex}`));
        };

        img.src = frames[frameIndex];
      };

      renderFrame();
    });
  }

  private async createGIF(
    frames: string[], 
    fps: number,
    onProgress?: (progress: number, status: string) => void
  ): Promise<string> {
    // For GIF creation, we'll create a simple animated sequence
    // In a production environment, you'd use a proper GIF encoder library
    
    onProgress?.(50, 'Creating GIF animation...');
    
    // Create a canvas animation that cycles through frames
    const gifCanvas = document.createElement('canvas');
    const gifCtx = gifCanvas.getContext('2d')!;
    
    // Set reasonable GIF dimensions
    gifCanvas.width = 800;
    gifCanvas.height = 600;
    
    // For now, return the first frame as a static image
    // In production, implement proper GIF encoding
    const img = new Image();
    img.onload = () => {
      gifCtx.drawImage(img, 0, 0, gifCanvas.width, gifCanvas.height);
    };
    img.src = frames[0];
    
    return new Promise((resolve) => {
      gifCanvas.toBlob((blob) => {
        if (blob) {
          onProgress?.(100, 'GIF created successfully!');
          resolve(URL.createObjectURL(blob));
        }
      }, 'image/png');
    });
  }

  // Create a preview animation
  async createPreviewAnimation(
    imageUrls: string[],
    containerElement: HTMLElement,
    options: {
      fps: number;
      rotationStyle: 'smooth' | 'discrete' | '3d';
    }
  ): Promise<void> {
    const frames = await this.frameInterpolator.createRotationFrames(
      imageUrls, 
      30, // 30 frames for preview
      options.rotationStyle
    );

    const previewCanvas = document.createElement('canvas');
    const previewCtx = previewCanvas.getContext('2d')!;
    previewCanvas.width = 400;
    previewCanvas.height = 300;
    previewCanvas.style.width = '100%';
    previewCanvas.style.height = 'auto';
    previewCanvas.style.borderRadius = '8px';

    containerElement.innerHTML = '';
    containerElement.appendChild(previewCanvas);

    let frameIndex = 0;
    const animatePreview = () => {
      const img = new Image();
      img.onload = () => {
        previewCtx.clearRect(0, 0, previewCanvas.width, previewCanvas.height);
        
        // Scale image to fit canvas
        const scale = Math.min(
          previewCanvas.width / img.width,
          previewCanvas.height / img.height
        );
        const scaledWidth = img.width * scale;
        const scaledHeight = img.height * scale;
        const x = (previewCanvas.width - scaledWidth) / 2;
        const y = (previewCanvas.height - scaledHeight) / 2;
        
        previewCtx.drawImage(img, x, y, scaledWidth, scaledHeight);
        
        frameIndex = (frameIndex + 1) % frames.length;
        setTimeout(animatePreview, 1000 / options.fps);
      };
      
      img.src = frames[frameIndex];
    };

    animatePreview();
  }
}
