"use client";

export class BackgroundRemover {
  // Improved client-side background removal using canvas and color thresholding
  async removeBackgroundCanvas(imageUrl: string): Promise<string> {
    return new Promise((resolve, reject) => {
      const img = new Image();
      img.crossOrigin = 'anonymous';
      
      img.onload = () => {
        const canvas = document.createElement('canvas');
        const ctx = canvas.getContext('2d')!;
        
        canvas.width = img.width;
        canvas.height = img.height;
        
        ctx.drawImage(img, 0, 0);
        
        const imageData = ctx.getImageData(0, 0, canvas.width, canvas.height);
        const data = imageData.data;
        
        // Calculate dominant background color from corners
        const corners = [
          this.getPixel(data, 0, 0, canvas.width),
          this.getPixel(data, canvas.width - 1, 0, canvas.width),
          this.getPixel(data, 0, canvas.height - 1, canvas.width),
          this.getPixel(data, canvas.width - 1, canvas.height - 1, canvas.width)
        ];
        
        const bgColor = this.getDominantColor(corners);
        const threshold = 40;
        
        // Remove pixels similar to background color
        for (let i = 0; i < data.length; i += 4) {
          const r = data[i];
          const g = data[i + 1];
          const b = data[i + 2];
          
          const diff = Math.sqrt(
            (r - bgColor.r) ** 2 +
            (g - bgColor.g) ** 2 +
            (b - bgColor.b) ** 2
          );
          
          if (diff < threshold) {
            data[i + 3] = 0; // Transparent
          }
        }
        
        ctx.putImageData(imageData, 0, 0);
        
        canvas.toBlob((blob) => {
          if (blob) {
            resolve(URL.createObjectURL(blob));
          } else {
            reject(new Error('Failed to create blob'));
          }
        }, 'image/png');
      };
      
      img.onerror = () => reject(new Error('Failed to load image'));
      img.src = imageUrl;
    });
  }

  private getPixel(data: Uint8ClampedArray, x: number, y: number, width: number) {
    const index = (y * width + x) * 4;
    return {
      r: data[index],
      g: data[index + 1],
      b: data[index + 2]
    };
  }

  private getDominantColor(colors: Array<{r: number, g: number, b: number}>) {
    const avg = colors.reduce((acc, c) => ({
      r: acc.r + c.r,
      g: acc.g + c.g,
      b: acc.b + c.b
    }), { r: 0, g: 0, b: 0 });
    
    return {
      r: Math.round(avg.r / colors.length),
      g: Math.round(avg.g / colors.length),
      b: Math.round(avg.b / colors.length)
    };
  }
}
